import moment from 'moment'

export function transformTab (tab, isTop, isGood) {
  if (isTop) {
    return ''
  } else if (isGood) {
    return ''
  } else {
    switch (tab) {
      case 'share':
        return ''
      case 'ask':
        return ''
      case 'job':
        return ''
      case 'top':
        return ''
      case 'dev':
        return ''
      default:
        return false
    }
  }
}
moment.locale('zh-cn')
export function transformDateFromNow (date) {
  return moment(date).startOf('day').fromNow()
}

export function transformDate (date) {
  return moment(date).format('YYYYMDD,a h:mm:ss')
}




